@import "https://fonts.googleapis.com/css2?family=Monoton&family=Quicksand:wght@500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  color: #5a3e2b;
  font-family: Quicksand;
  font-size: 2.4rem;
  font-weight: 500;
}

.app {
  grid-template-rows: auto auto 1fr auto;
  width: 100%;
  height: 100vh;
  display: grid;
}

h1 {
  text-align: center;
  text-transform: uppercase;
  word-spacing: 30px;
  letter-spacing: -5px;
  background-color: #f4a226;
  padding: 2.4rem 0;
  font-family: Monoton;
  font-size: 8rem;
  font-weight: 400;
}

.add-form {
  background-color: #e5771f;
  justify-content: center;
  align-items: center;
  gap: .8rem;
  padding: 2.8rem 0;
  display: flex;
}

h3 {
  margin-right: 1.6rem;
  font-size: 2.4rem;
}

button, select, input {
  color: #5a3e2b;
  cursor: pointer;
  background-color: #ffebb3;
  border: none;
  border-radius: 10rem;
  padding: 1.2rem 3.2rem;
  font-family: inherit;
  font-size: 1.8rem;
  font-weight: 700;
}

.add-form button {
  text-transform: uppercase;
  background-color: #76c7ad;
}

.list {
  color: #ffebb3;
  background-color: #5a3e2b;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 3.2rem;
  padding: 4rem 0;
  display: flex;
}

.actions button, .list select {
  text-transform: uppercase;
  margin: 0 .8rem;
  padding: .8rem 2.4rem;
  font-size: 1.4rem;
  font-weight: 700;
}

.list ul {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  place-content: start center;
  gap: 1.2rem;
  width: 80%;
  list-style: none;
  display: grid;
  overflow: scroll;
}

.list li {
  align-items: center;
  gap: 1.2rem;
  display: flex;
}

input[type="checkbox"] {
  accent-color: #e5771f;
  width: 2rem;
  height: 2rem;
}

.list li button {
  cursor: pointer;
  background: none;
  border: none;
  padding: .8rem;
  font-size: 1.8rem;
  transform: translateY(2px);
}

.stats {
  text-align: center;
  background-color: #76c7ad;
  padding: 3.2rem 0;
  font-weight: 700;
}
